.labelCbx {
    user-select: none;
    cursor: pointer;
    margin-bottom: 0;
}

.labelCbx input:checked + .checkbox {
    border-color: #20C2E0;
}

.labelCbx input:checked + .checkbox svg path {
    fill: #20C2E0;
}

.labelCbx input:checked + .checkbox svg polyline {
    stroke-dashoffset: 0;
}

.labelCbx:hover .checkbox svg path {
    stroke-dashoffset: 0;
}

.labelCbx .checkbox {
    position: relative;
    top: 2px;
    float: left;
    margin-right: 8px;
    width: 20px;
    height: 20px;
    border: 2px solid #C8CCD4;
    border-radius: 3px;
}

.labelCbx .checkbox svg {
    position: absolute;
    top: -2px;
    left: -2px;
}

.labelCbx .checkbox svg path {
    fill: none;
    stroke: #20C2E0;
    stroke-width: 2;
    stroke-linecap: round;
    stroke-linejoin: round;
    stroke-dasharray: 71px;
    stroke-dashoffset: 71px;
    transition: all .6s ease;
}

.labelCbx .checkbox svg polyline {
    fill: none;
    stroke: #FFF;
    stroke-width: 2;
    stroke-linecap: round;
    stroke-linejoin: round;
    stroke-dasharray: 18px;
    stroke-dashoffset: 18px;
    transition: all .3s ease;
}

.labelCbx > span {
    pointer-events: none;
    vertical-align: middle;
}



.invisible {
    position: absolute;
    z-index: -1;
    width: 0;
    height: 0;
    opacity: 0;
}
